const Market = ({ name, curr_price, img, percentage }) => {
  return (
    <>
      <div className="flex">
        <div className="flex-child">
          <img src={img} width="50px" height="100%" />
        </div>
        <div className="flex-child">{name}</div>
        <div className="flex-child">{curr_price}</div>
        {percentage >= 1 ? (
          <div className="flex-child" style={{ color: "green" }}>
            {percentage}%
          </div>
        ) : (
          <div className="flex-child" style={{ color: "red" }}>
            {percentage}%
          </div>
        )}
      </div>
    </>
  );
};

export default Market;
